html,body{height:100%;overflow:hidden;font-weight:400;font-family:"Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif}html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent;list-style:none;box-sizing:border-box}body{line-height:1.2}:focus{outline:1}article,aside,canvas,details,figcaption,figure,footer,header,hgroup,menu,nav,section,summary{display:block}nav ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}a{margin:0;padding:0;border:0;font-size:100%;vertical-align:baseline;background:transparent;text-decoration:none;color:inherit}ins{background-color:#ff9;color:#000;text-decoration:none}mark{background-color:#ff9;color:#000;font-style:italic;font-weight:bold}del{text-decoration:line-through}abbr[title],dfn[title]{border-bottom:1px dotted #000;cursor:help}table{border-collapse:collapse;border-spacing:0}hr{display:block;height:1px;border:0;border-top:1px solid #cccccc;margin:1em 0;padding:0}input,select{vertical-align:middle}img[src=""],img:not([src]){opacity:0}body{color:var(--font-color);-webkit-tap-highlight-color:rgba(0,0,0,0)}::-webkit-scrollbar{width:5px;height:5px}::-webkit-scrollbar-track{background-color:var(--menu-bgcolor)}::-webkit-scrollbar-thumb{background-color:var(--scrollbar-color)}.Mui-focused{color:var(--input-color) !important}.MuiInput-underline:after{border-color:var(--input-color) !important}

.layout{display:flex;height:100%}.layout .container{display:flex;flex-direction:column;flex:1 1}

.song-list-card{flex-direction:column;height:100%;margin-bottom:10px !important;padding:5px;cursor:pointer}.song-list-card .song-list-img-warp{position:relative;width:100%;height:0;padding-bottom:100%;border-radius:5px;overflow:hidden}.song-list-card .song-list-img-warp:hover .song-list-img{width:130%;height:130%}.song-list-card .song-list-img-warp .song-list-img{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:100%;height:100%;-webkit-transition:all 1.5s;transition:all 1.5s}.song-list-card .song-list-img-warp .song-list-img-cover{position:absolute;top:0;left:0;width:100%}.song-list-card .song-list-name{font-size:14px;display:-webkit-box;overflow:hidden;text-overflow:ellipsis;-webkit-line-clamp:2;-webkit-box-orient:vertical;margin-top:10px}

.title-wrap{padding:20px 10px 10px 10px}.title-wrap .title{font-size:18px;font-weight:500}

.song-card{align-items:center;height:100%;margin-bottom:10px !important;padding:5px;border-radius:10px;cursor:pointer}.song-card:hover{box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)}.song-card .song-id{flex-basis:30px;text-align:center;margin-right:10px}.song-card .song-img-warp{position:relative;width:80px;height:80px;overflow:hidden}.song-card .song-img-warp .song-img{width:100%;height:100%;border-radius:5px}.song-card .song-img-warp .song-img-cover{position:absolute;top:0;left:0;width:100%}.song-card .song-warp{display:flex;flex:1 1;flex-direction:column;margin-left:10px}.song-card .song-warp .song-name{font-size:16px;font-weight:bold;display:-webkit-box;overflow:hidden;text-overflow:ellipsis;-webkit-line-clamp:1;-webkit-box-orient:vertical}.song-card .song-warp .song-detail{font-size:14px;margin-top:10px;display:-webkit-box;overflow:hidden;text-overflow:ellipsis;-webkit-line-clamp:1;-webkit-box-orient:vertical}

.discovery{max-width:1200px;margin:0 auto;padding:10px 14px}

.header{display:flex;align-items:center;width:100%;height:60px;background-color:var(--header-bgcolor)}.header .toolbar{width:100%;justify-content:space-between}

.menu{flex-basis:300px;background-color:var(--menu-bgcolor);box-shadow:0 10px 30px -12px rgba(0,0,0,0.42),0 4px 25px 0px rgba(0,0,0,0.12),0 8px 10px -5px rgba(0,0,0,0.2);overflow:auto;z-index:10}.menu .MuiPaper-root{width:300px !important}.MuiDrawer-modal .MuiPaper-root{width:80vw !important}.menu-avatar-container .avatar{width:60px !important;height:60px !important;margin-right:20px}.user-icon{font-size:60px !important;margin-right:20px}

.main{flex:1 1;background-color:var(--main-bgcolor);overflow:auto}

.form{margin-top:20px !important}.dialog-actions{margin-top:50px}

.dialog-title{margin-left:16px !important}

.form{margin-top:20px !important}.avatar-input{display:none}.login-avatar-container{display:flex;width:100%;height:100px;justify-content:center}.login-avatar-container .avatar{width:100px !important;height:100px !important}.dialog-actions{margin-top:50px}


